import React, { Component } from 'react'
import glamorous from 'glamorous'
import titleize from 'titleize';

import { Wrapper } from './Grid'
import StepCounter from './StepCounter'

import hap_img from './hap.png'


const ContentDiv = glamorous.div({
  paddingTop: '50px',
  paddingBottom: '50px',
  display: 'flex',
  alignItems: 'center',
  flexDirection: 'column'
})

class RecordStepTwo extends Component {

  renderThanks = () => {

    return (
        <Wrapper>

          <StepCounter steps={this.props.steps} currentStep={this.props.currentStep} />

          <ContentDiv>
              <p>Thank you for helping!</p>
          </ContentDiv>

          <div>
            <img src={hap_img} alt="logo" width="146px" height="124px" />
          </div>

        </Wrapper>
      )
  }

  render() {

    return (
      <Wrapper>

        <StepCounter steps={this.props.steps} currentStep={this.props.currentStep} />

        <ContentDiv>
          { this.renderThanks()}
        </ContentDiv>

      </Wrapper>
    )
  }

}

export default RecordStepTwo



