import React, { Component } from 'react'
import glamorous from 'glamorous'
import titleize from 'titleize';

import { Button } from 'sdx-react'

import { Wrapper } from './Grid'
import StepCounter from './StepCounter'

const ContentDiv = glamorous.div({
  paddingTop: '50px',
  paddingBottom: '50px',
  display: 'flex',
  alignItems: 'center',
  flexDirection: 'column'
})

class Main extends Component {

  render() {
    return (
      <Wrapper>

        <ContentDiv>
            <p>Hello, welcome to the Speech Emotion Recognition platform</p>
            <p>To proceed to emotion evaluation, click on <strong> Evaluation </strong>.</p>
        </ContentDiv>

        <glamorous.Div>
          <Button onClick={() => this.props.setApp("demo")} label="Evaluation" icon="004-arrow-right" primary />
        </glamorous.Div>

        <ContentDiv>
            <p>To help make this demo better and record your own emotions, click on <strong> Record </strong>.</p>
        </ContentDiv>

        <glamorous.Div>
          <Button onClick={() => this.props.setApp("record")} label="Record" icon="004-arrow-right" primary />
        </glamorous.Div>

      </Wrapper>
    )
  }

}

export default Main



