import React, { Component } from 'react'
import { v1 } from 'uuid'

import Header from './Header'
import Footer from './Footer'

import Main from './Main'
import DemoStepOne from './DemoStep1'
import DemoStepTwo from './DemoStep2'
import RecordStepOne from './RecordStep1'
import RecordStepTwo from './RecordStep2'


class App extends Component {

  constructor(props) {
    super(props)
    
    this.state = {
      uuid: v1(),
      currentStep: 0,
      name: '',
      app: "menu"

    }

  }

  getCurrentStep = () => {
    switch(this.state.app) {
      case "menu":
        return <Main setApp={this.setApp} steps="1" currentStep="1" />
        break;
      case "demo":
        if(this.state.currentStep == 0) {
          return <DemoStepOne nextStep={this.nextStep} steps="2" currentStep="1" setVerificationPhase={this.setVerificationPhase.bind(this)} />
          }
        if(this.state.currentStep == 1) {
          return <DemoStepTwo steps="2" currentStep="2" getVerification={this.getVerification.bind(this)} />
          }
        break;
      case "record":
        if(this.state.currentStep == 0) {
          return <RecordStepOne nextStep={this.nextStep} steps="2" currentStep="1"/>
          }
        if(this.state.currentStep == 1) {
          return <RecordStepTwo steps="2" currentStep="2"/>
          }
        break;
    }

    this.setState({...this.state, app:"menu", currentStep: 0})

    return <Main setApp={this.setApp} steps="1" currentStep="1"/>
  }

  nextStep = () => {
    const next = this.state.currentStep + 1
    this.setState({ ...this.state, currentStep: next })
  }

  previousStep = () => {
    const next = this.state.currentStep - 1
    this.setState({ ...this.state, currentStep: next })
  }

  setApp = (app) => {
    this.setState({...this.state, app: app})
    }

  getName = () => this.state.name

  setName = (name) => {
    this.setState({ ...this.state, name })
  }

  getVerification = () => this.state.verification

  setVerificationPhase = (result) => {
    
    this.setState({ ...this.state, verification: result })
  }

  render() {
    return (
      <div className="main-wrapper">
        <Header/>
        <div className="page">
          {this.getCurrentStep()}
        </div>
        <Footer/>
      </div>
    );
  }
}

export default App;



